.form {
    min-height: 80vh;
    box-sizing: border-box;
    display: flex;
    position: relative;
}

.left .title {
    font-size: 2rem;
}

.left {
    background-image: linear-gradient(to right, rgba(6, 157, 82, .7), rgba(9, 236, 122, .6));
    padding: 5em;
    color: white;
    width: 100vw;
}

.right {
    position: absolute;
    right: 0;
    clip-path: polygon(10% 0, 100% 0, 100% 100%, 0% 100%);
    height: 80vh;
    width: 50vw;
    background-color: white;
    text-align: center;
}

.container {
    display: flex;
}

.step {
    display: flex;
    flex-direction: column;
    margin: 2em;
    text-align: center;
    color: grey;
}

.fa {
    font-size: 5rem;
}

.done {
    color: #ccc;
}

.active {
    color: white;
}

.form-step {
    margin-left: 25%;
    text-align: left;
}

.radio {
    display: none;
}

.radio-label {
    display: block;
    cursor: pointer;
    padding-left: 4.5em;
    position: relative;
    padding: 2em;
    position: relative;
    font-size: 1.5rem;
}

.radio-button {
    height: 3rem;
    width: 3rem;
    border: 5px solid rgb(9, 236, 122);
    border-radius: 50%;
    display: inline-block;
    position: relative;
}

.radio-button::after {
    content: "";
    display: block;
    height: 1.3rem;
    width: 1.3rem;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(9, 236, 122);
    opacity: 0;
    transition: opacity .2s;
}

.radio:checked ~ .radio-label > .radio-button::after {
    opacity: 1;
}

.radio {
    display: none;
}

.location {
    position: relative;
    top: -20px;
    left: 10px;
}

.treename > :not(a){
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.treename > label {
    top: 40%;
    font-size: 3rem;
    background-image: linear-gradient(to right, rgba(6, 157, 82), rgba(9, 236, 122));
    color: transparent;
    background-clip: text;
} 

.next, .submit {
    position: absolute;
    right: 2em;
    bottom: 1em;
}

.back {
    position: absolute;
    left: 2em;
    bottom: 1em;
}

.next, .back, .submit {
    background-color: rgb(9, 236, 122);
    color: white;
    padding: 1em 2em;
    font-size: 1.5rem;
    border: none;
    border-radius: 50px;
    cursor: pointer;    
}

.next:hover {
    background-color: white;
    color: rgb(9, 236, 122);
    outline: 2px solid rgb(9, 236, 122);
}

.text {
    border: none;
    border-bottom: 2px solid rgb(9, 236, 122);
    font-size: 2em;
    outline: none;
}

.textone {
    width: 50%;
}

.textthree {
    width: 30%;
}

.label {
    display: block;
    margin-top: 3em;
}

.no-block {
    display: flex;
}

.congratulations {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.congratulations h3 {
    display: inline-block;
    color: transparent;
    background-image: linear-gradient(to right, rgba(6, 157, 82), rgba(9, 236, 122));
    background-clip: text;
    font-size: 3em;
}

.congratulations p {
    font-size: 1.5em;
}

.check {
    font-size: 1.5rem;
}