header {
    min-height: 80vh;
    background-image: linear-gradient(45deg, rgba(80, 80, 80, 1), 30%, transparent),url('../assets/images/trees.jpg');
    background-size: cover;
    color: white;
    padding: 3em;
    box-sizing: border-box;
}

header h1 {
    font-size: 2rem;
    font-family: 'Kanit';
}

header p {
    font-size: 3.8rem;
    width: 50%;
}

header a {
    background-image: linear-gradient(to right, rgb(6, 157, 82), rgb(9, 236, 122));
    text-decoration: none;
    font-size: 1.5rem;
    padding: 1em 2em;
    border-radius: 50px;
    color: white;
}

header a:hover {
    background-image: none;
    background-color: white;
    color: rgb(9, 236, 122);
}

.logo {
    width: 50px;
}